@font-face {
  font-family: 'fontello';
  src: url('icons/font/fontello.eot?1681777');
  src: url('icons/font/fontello.eot?1681777#iefix') format('embedded-opentype'),
       url('icons/font/fontello.woff2?1681777') format('woff2'),
       url('icons/font/fontello.woff?1681777') format('woff'),
       url('icons/font/fontello.ttf?1681777') format('truetype'),
       url('icons/font/fontello.svg?1681777#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'fontello';
